@import url(https://fonts.googleapis.com/css2?family=Staatliches&display=swap);
@import url(https://fonts.googleapis.com/css?family=Inconsolata&display=swap);
/* Normalization*/
*, *::after, *::before
{
  margin: 0;
  padding: 0;
  box-sizing: inherit
}

html {
  height: 100%;
  font-size: 62.5%;
}
@media only screen and (max-width: 62.5em) {
  html {
    font-size: 55.5%;
  }
}
@media only screen and (max-width: 37.5em) {
  html {
    font-size: 50%;
  }
}
@media only screen and (min-width: 143.75em) {
  html {
    font-size: 90%;
  }
}
@media only screen and (min-width: 187.5em) {
  html {
    font-size: 110%;
  }
}
@media only screen and (min-width: 237.5em) {
  html {
    font-size: 120%;
  }
}

body {
  background: #22262A;
  box-sizing: border-box;
  height: 100%;
  overflow-x: hidden;
  font-family: 'Staatliches', 'Inconsolata', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: white;
}

main {
  width: 90%;
  margin: 2.5rem auto 8rem;

  -webkit-user-select: none; /* Safari */ /* Firefox */
  -ms-user-select: none; /* IE10+/Edge */
  user-select: none; /* Standard */

}

@media only screen and (max-width: 62.5em) {
  main {
    width: 85%;
  }
}

@media only screen and (max-width: 31.25em) {
  main {
    width: 78%
  }
}

@media only screen and (min-width: 87.5em) {
  main {
    width: 120rem
  }
}



::-webkit-scrollbar-track
{
  border-radius: 12px;
  background-color: #171A1D;
}

::-webkit-scrollbar
{
  width: 6px;
  background-color: #171A1D;
  border-radius: 3px;
}

::-webkit-scrollbar-thumb
{
  border-radius: 12px;
  background-color: #97BFD3;
}

.about{
    height: 70vh;
    text-align: center;
}

.title {
    margin: 0;
    font-size: 10.5rem;
    color: #fff;
    letter-spacing: 4px;
}

@media only screen and (max-width: 62.5em) {
    .title {
        font-size: 8rem
    }
}

@media only screen and (max-width: 44.375em) {
    .title {
        font-size: 5rem;
        margin-bottom: 1rem;
    }
}

@media only screen and (max-width: 31.25em) {
    .title span {
        display: block;
    }
}


.sub-title {
    color: #94A4B4;
    font-size: 3rem;
    word-spacing: 0.2em;
}
.button {
    display: block;
    margin-top: 8rem;
    height: 5rem;
    width: 16rem;
    overflow: hidden;
    text-decoration: none;
}

.button * {
    transition: all 150ms;
    display: block;
    width: 100%;
    height: 4.5rem;
    line-height: 1;
    font-size: 2rem;
    border: 2px solid #fff;
    background-color: #22262b;
    color: #fff;
    font-family: 'Staatliches', sans-serif;
}

@media only screen and (min-width: 200em) {
    .button{
        height: 3rem;
        width: 10rem;
    }
}

.button_bottom {
    margin-top: 1rem;
    background-color: #fff;
    color: #22262a;
}

.button:hover * {
    transform: translateY(-5.5rem);
    cursor: pointer;
}
.subtitle {
    color: #94a4b4;
    line-height: 1;
    font-size: 3rem;
    display: block;
}
@media only screen and (max-width: 44.375em) {
    .subtitle {
        font-size: 2rem;
    }
}
.word {
    display: inline-block;
    vertical-align: top;
    margin: 0 0.5rem;
    height: 4rem;
    overflow: hidden;
}
@media only screen and (max-width: 44.375em) {
    .word {
        height: 2.5rem;
        margin: 0 0.3rem;
    }
}
.word * {
    transition: all 250ms;
}
.word-bottom {
    visibility: hidden;
}
.word-active * {
    transform: translateY(-4rem);
}
@media only screen and (max-width: 44.375em) {
    .word-active * {
        transform: translateY(-3rem);
    }
}
.word-active .word-bottom {
    visibility: visible;
}
.word-top {
    margin-bottom: 1rem;
}
.word-bottom {
    color: #5b6570;
}
.portfolio-title
{
    display: inline-block;
    font-size: 4.0rem;
    color: #fff;
    letter-spacing: 2px;
    -webkit-user-select: text;
        -ms-user-select: text;
            user-select: text;
}

.portfolio-sub {
    font-family: 'Inconsolata', sans-serif;
    font-size: 1.6rem;
    color: #94a4b4;
    display: inline-block;
    margin: 1.2rem 0;
    line-height: 1.5;
    -webkit-user-select: text;
        -ms-user-select: text;
            user-select: text;
}
.portfolio {margin-top: 4rem}
.project {
    overflow: hidden;
    box-shadow: 0 0 10px #000;
}

.project-item {
    position: relative;
}

.project:hover .project-overlay {
    opacity: 1;
    visibility: visible;
}

.project:hover .project-img {
    transform: scale(1.1);
}

.project-img {
    width: 100%;
    transition: all 200ms;
}

.project-img-modal {
    object-fit: cover;
    width: 100%;
    height: 100%;
}

@media only screen and (max-width: 45em) {
    .project-img-modal {
        object-fit: contain;
    }
}

.project-overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: radial-gradient(transparent, rgba(0, 0, 0, .5));
    transition: all 200ms;
    opacity: 0;
    visibility: hidden;
    text-align: center;
}

.project-buttons {
    position: absolute;
    bottom: 1.5rem;
    left: 50%;
    transform: translateX(-50%);
    width: 100%;
}

@media only screen and (max-width: 31.25em) {
    .project-buttons {
        bottom: 0.5rem;
    }
}

.project-button {
    display: inline-block;
    text-decoration: none;
    color: #22262a;
    font-family: Staatliches, sans-serif;
    font-size: 1.8rem;
    letter-spacing: 1px;
    margin: 0 1rem;
    background-color: rgba(255, 255, 255, .8);
    border: none;
    border-radius: 3px;
    padding: 0.5rem 2rem;
    cursor: pointer;
    transition: all 200ms;
}

@media only screen and (max-width: 31.25em) {
    .project-button {
        font-size: 1.6rem;
    }
}

.project-button:disabled, .project-button:disabled:hover {
    background-color: rgba(255, 255, 255, .4);
    cursor: default;
}

.project-button svg {
    margin-right: 0.5rem;
}

.project-button:hover, .project-button:focus, .project-button:active {
    background-color: rgba(255, 255, 255, 1);
}

.project-button-modal {
    margin: 2rem 0.5rem 0 0.5rem;
    font-size: 1.4rem;
    padding: 0.5rem 1.5rem;
}

.project-background{
    position: fixed;
    top: 0;
    left: 0;
    z-index: 800;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0, .3);
    opacity: 0;
    visibility: hidden;
    transition: all 400ms cubic-bezier(0.175, 0.885, 0.32, 1.275);
}

.project-background-show{
    opacity: 1;
    visibility: visible;
}

.project-modal {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 100rem;
    height: 50rem;
    background-color: #343a41;
    -webkit-user-select: text;
        -ms-user-select: text;
            user-select: text;
    box-shadow: 0 0 5px #000;
    opacity: 0;
    transition: all 400ms 200ms cubic-bezier(0.175, 0.885, 0.32, 1.275);
    transform: translate(-50%, -50%) scale(.25);
}

.project-modal-show {
    opacity: 1;
    transform: translate(-50%, -50%) scale(1);
}

@media only screen and (max-width: 56.25em) {
    .project-modal {
        width: 95%;
        min-height: 50rem;
    }
}

@media only screen and (max-width: 45em) {
    .project-modal {
        width: 43rem;
        height: auto;
    }
}

@media only screen and (max-width: 25em) {
    .project-modal {
        width: 38rem;
    }
}

.project-close {
    position: absolute;
    top: 1.5rem;
    right: 1.5rem;
    background-color: transparent;
    border: none;
    color: #94a4b4;
    font-size: 3rem;
    cursor: pointer;
    transition: all 200ms cubic-bezier(0.175, 0.885, 0.32, 1.275);
}

.project-close:hover {
    transform: scale(1.2)
}

.project-left {
    width: 60rem;
    height: 100%;
    display: inline-block;
}

@media only screen and (max-width: 56.25em) {
    .project-left {

        width: 55%;
    }

}

@media only screen and (max-width: 45em) {
    .project-left {

        width: 100%;
        height: auto;
    }

}

.project-right {
    width: calc(100% - 60.5rem);
    display: inline-block;
    vertical-align: top;
    padding: 1.5rem;
    color: #fff;
}

@media only screen and (max-width: 56.25em) {
    .project-right {

        width: 45%;
    }
}

@media only screen and (max-width: 45em) {
    .project-right {

        width: 100%;

    }
}

.project-label {
    font-size: 1.6rem;
    color: #94a4b4;
    letter-spacing: 1px;
}

.project-name {
    font-size: 3rem;
    letter-spacing: 1px;
    font-weight: lighter;
}

.project-skills {
    list-style: none;
    margin-bottom: 2rem;
}

.project-skill {
    display: inline-block;
    margin: .5rem;
    border: 1px solid #22262a;
    padding: .5rem;
    color: #94a4b4;
    font-weight: lighter;
    letter-spacing: 1px;
    font-size: 1.3rem;
}

.project-summary {
    margin-top: 1rem;
    font-family: Inconsolata, sans-serif;
    font-size: 1.4rem;
    line-height: 1.5;
    color: #94a4b4;
    overflow-y: scroll;
    height: 22rem;
    white-space: pre-line;
}

.project-summary::-webkit-scrollbar-track {
    background-color: #252a2e;
}

.header{
    padding: 1.5rem;
    -webkit-user-select: none; /* Safari */ /* Firefox */
    -ms-user-select: none; /* IE10+/Edge */
    user-select: none; /* Standard */
}

.brand_logo{
    display: block;
    width: 10rem;
    height: 8rem;
}

@media only screen and (min-width: 100em){

    .brand_logo{
        width: 15rem;
        height: 12rem;
    }

}

.brand_logo img{
    width: 100%;
    height: 100%;
}
.contact {
    position: absolute;
    top: 50%;
    left: -7rem;
    transform: translateY(-50%);
    z-index: 700;
}
@media only screen and (min-width: 100em) {
    .contact {
        left: -12.5rem;
    }
}
.contact-list {
    list-style: none;
}
.contact-link:visited, .contact-link:link {
    display: block;
    color: #fff;
    font-size: 2.5rem;
    padding: 1.2rem;
    transition: all 450ms cubic-bezier(0.175, 0.885, 0.32, 1.275);
    text-decoration: none;
}
@media only screen and (max-width: 31.25em) {
    .contact-link:visited, .contact-link:link {
        padding: 1rem;
    }
}
@media only screen and (min-width: 100em) {
    .contact-link:visited, .contact-link:link {
        padding: 1.5rem;
        font-size: 2.8rem;
    }
}
.contact-link-linkedin {
    background-color: #0077b5;
}
.contact-link-github {
    background-color: #333;
}
.contact-link-email {
    background-color: #6fc2b0;
}
.contact-link-resume {
    background-color: #565f69;
}
.contact-link:hover, .contact-link:active, .contact-link:focus {
    transform: translateX(6.4rem);
}
.contact-label {
    display: inline-block;
    vertical-align: middle;
    font-size: 1.6rem;
    margin-right: 1.2rem;
    width: 5.8rem;
    letter-spacing: 1px;
    text-align: right;
}
@media only screen and (min-width: 100em) {
    .contact-label {
        width: 10.8rem;
    }
}


.footer{
    background-color: #171A1D;
    padding: 3rem;
}

.footer span {
    font-size: 1.5rem;
    font-family: 'Inconsolata', sans-serif;
}

.white-text {
    color: white;
}

.gray-text {
    color: #94A4B4;
}
